.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.section{
  display: flex;
  flex-direction: column;
justify-content: center;
align-items: center;
width: 100vw;
height: 100vh;
}
#jump{
  position: relative;
  top: 30vh;
  }


a{
  text-decoration: none !important;
}
.containerAbout{
background-color: rgb(127, 218, 157);
}
